.home-section-intro-container {
  @include section();
  height: 100vh;
  position: relative;
  background: url("../../assets/wave-logo.png") no-repeat center;
  background-size: 100vh;
  mix-blend-mode: hard-light;
  margin-top: $gutterWidth*2;
  background-color: $primaryBackgroundColor;

  .home-section-intro {
    @include display-column();
    align-items: center;
    justify-content: space-between;
    height: $gutterWidth*16;
    margin-bottom: $gutterWidth*2;

  }

  .home-section-paragraph {
    font-size: $normalFontSize;
    line-height: $normaLineHeight;
    text-align: center;
    font-weight: $heading;
    color: #FFFFFF;
    letter-spacing: $letterSpacing;
  }

  .home-section-intro-link {
    .home-section-intro-button {
      @include btn-standard();
      width: $gutterWidth*6;
    }
  }
}

.home-section-about-container {
  @include section();
  padding: $gutterWidth 0;
  position: relative;
  background: url("../../assets/wave-ripple.svg") no-repeat center;
  background-size: cover;

  .home-section-content-container {
    @include display-column();
    justify-content: center;
    align-items: center;
    margin: $gutterWidth 0;
    z-index: 2;

    .section-paragraph {
      margin: $gutterWidth;

    }
  }
}

.home-section-event-container {
  @include section();
  padding: $gutterWidth*2 $gutterWidth*4;
  background-color: $primaryBackgroundColor;

  .section-image-heading {
    margin-top: $gutterWidth*2;
  }

  .home-section-event-card-container {
    display: grid;
    width: 100%;
    margin: $gutterWidth 0;

    @include desktop-1281-1920() {
      width: calc((#{$cardWidth} + #{$gutterWidth})*4);
      grid-template-columns: 25% 25% 25% 25%;
    }

    @include desktop-1025-1280 {
      grid-template-columns: 33.33% 33.33% 33.33%;

    }

    @include desktop-768-1024 {
      grid-template-columns: 50% 50%;

    }

    @include desktop-320-767 {
      @include display-column();
      align-items: center;

    }

    .home-section-event-card {
      @include display-column();
      justify-content: space-between;
      align-items: center;
      position: relative;
      width: $cardWidth;
      height: $cardHeight;
      overflow: hidden;
      margin: 0 auto $gutterWidth auto;

      .home-section-event-card-background-tint {
        position: absolute;
        z-index: 1;
        width: $cardWidth;
        height: $cardHeight;
        background: linear-gradient(180deg, #000000 -53.27%, rgba(0, 0, 0, 0.3) 132.69%);
      }

      .home-section-event-card-background {
        position: absolute;
        width: $cardWidth;
        height: $cardHeight;
        z-index: 0;
        background-color: $backgroundImageColor;
      }

      .home-section-event-card-heading {
        z-index: 2;
        font-style: normal;
        font-weight: 500;
        font-size: $headerFontSize*0.8;
        line-height: $headerLineHeight*0.8;
        text-align: center;
        color: #FFFFFF;
        margin-top: $gutterWidth;

        &.coming-soon {
          text-transform: uppercase;
          margin-bottom: $gutterWidth;
        }
      }

    }
  }
}

.previous-speakers-section-container {
  @include section();
  padding: $gutterWidth*2 $gutterWidth*10;
  background: url("../../assets/wave-ripple.svg") no-repeat center;
  background-size: cover;

  .previous-speakers-section-card-container {
    z-index: 2;

    display: grid;
    width: 100%;
    margin: $gutterWidth 0;
    grid-template-columns: 33.33% 33.33% 33.33%;

    @include desktop-1025-1280 {
      grid-template-columns: 33.33% 33.33% 33.33%;

    }

    @include desktop-768-1024 {
      grid-template-columns: 50% 50%;

    }

    @include desktop-320-767 {
      @include display-column();
      align-items: center;
    }

    .previous-speakers-section-card {
      @include desktop-320-767 {
        @include display-row();
        width: 100%;
        justify-content: flex-start;
        padding: 0 $gutterWidth 0 $gutterWidth*2;
      }

      @include display-column();
      justify-content: center;
      align-items: center;
      margin: $gutterWidth*1.6;

      .previous-speakers-section-card-image {
        @include desktop-320-767 {
          margin: 0;

        }

        border-radius: 50%;
        width: $cardWidth*0.5;
        background-color: $secondaryBackgroundColor;
        margin-bottom: $gutterWidth*0.8;
      }

      .previous-speakers-section-card-content {
        @include desktop-320-767 {
          width: 100%;
          padding: 0 $gutterWidth;

        }


        .previous-speakers-section-card-heading {
          @include desktop-320-767 {
            @include display-row();
            text-align: left;

          }

          font-weight: bold;
          font-size: $normalFontSize*1.2;
          line-height: $normaLineHeight;
          text-align: center;
          color: $whiteText;
        }

        .previous-speakers-section-card-subheading {
          @include desktop-320-767 {
            @include display-row();
            text-align: left;

          }

          font-size: $normalFontSize*0.8;
          line-height: $normaLineHeight;
          text-align: center;
          color: $whiteText;
        }
      }
    }
  }
}

.ca-section-intro-container {
  @include section();

  @include desktop-320-767 {
    padding: $gutterWidth 0;
    min-height: 80vh;
    justify-content: unset;

  }

  @include desktop-768-1024 {
    padding: $gutterWidth*2 0;
  }

  @include desktop-1025-1280 {
    padding: $gutterWidth*4 0;
  }


  position: relative;
  height: 100vh;
  background: url("../../assets/ca-intro.png") no-repeat center;
  background-size: cover;
  margin-top: $gutterWidth*2;
  justify-content: space-between;
  padding: $gutterWidth*8 0;

  .ca-section-intro-background-tint {
    position: absolute;
    z-index: 0;
    width: 100%;
    height: inherit;
    top: 0;
    background: linear-gradient(180deg, #000000 0%, rgba(0, 0, 0, 0.3) 149.7%);

  }

  .ca-section-intro-heading {
    @include desktop-320-767 {
      height: $gutterWidth;
      margin: $gutterWidth*2;
    }

    z-index: 2;
    height: $gutterWidth*1.6;

  }

  .ca-section-intro-paragraph {

    @include desktop-320-767 {
      padding: 0 $gutterWidth;
      font-size: $normalFontSize;
      line-height: $normaLineHeight;

    }

    @include desktop-768-1024 {
      padding: 0 $gutterWidth*2;
    }

    @include desktop-1025-1280 {
      padding: 0 $gutterWidth*4;
    }
    z-index: 2;
    color: #FFFFFF;
    font-size: $normalFontSize*1.6;
    font-style: normal;
    font-weight: normal;
    line-height: $normaLineHeight*1.6;
    text-align: center;
    padding: 0 $gutterWidth*8;
  }

  .ca-section-intro-link {
    z-index: 1;

    @include desktop-320-767 {
      margin: $gutterWidth*2;


    }

    .ca-section-intro-button {
      @include btn-standard();
      width: $gutterWidth*6;
    }
  }
}

.ca-section-about-container {
  @include section();
  padding: $gutterWidth 0;
  position: relative;
  background: url("../../assets/wave-ripple.svg") no-repeat center;
  background-size: cover;

  .ca-section-content-container {
    @include desktop-320-767 {
      padding: 0 $gutterWidth*2;

    }

    @include desktop-768-1024 {
      padding: 0 $gutterWidth*2;
    }

    @include display-column();
    margin: $gutterWidth*2 0;
    padding: 0 $gutterWidth*12;
    text-align: left;
    justify-content: flex-start;
    align-items: flex-start;

    .section-image-heading {
      align-content: flex-start;
      margin-left: 0;
    }

    .ca-section-paragraph {
      @include desktop-320-767 {
        font-size: $normalFontSize;
        line-height: $normaLineHeight;

      }

      font-style: normal;
      font-weight: normal;
      font-size: $normalFontSize*1.4;
      line-height: $normaLineHeight*1.2;
      color: #FFFFFF;

    }
  }
}

.footer-section-container {
  z-index: 2;
  @include section();
  @include display-row();
  justify-content: space-between;
  padding: $gutterWidth*3 $gutterWidth*6;
  background-color: $primaryBackgroundColor;
  align-items: flex-start;

  @include desktop-1025-1280 {
    padding: $gutterWidth*3 $gutterWidth*4;


  }

  @include desktop-768-1024 {
    padding: $gutterWidth*2 $gutterWidth*2;


  }

  @include desktop-320-767 {
    @include display-column();
    min-height: fit-content;
    padding: $gutterWidth*2 $gutterWidth*2 0 $gutterWidth*2;
  }

  .footer-section-logo {
    @include desktop-320-767 {
      margin-bottom: $gutterWidth*2;
      width: $gutterWidth*4;

    }

    width: $gutterWidth*10;
  }

  .footer-section-contact-container {
    @include desktop-320-767 {
      @include display-row();
      width: 100%;
      min-height: fit-content;
      justify-content: unset;
      height: fit-content;
      margin: 0 0 $gutterWidth 0;
      font-size: $normalFontSize*0.8;

    }

    @include display-column();
    justify-content: space-between;
    font-size: $normalFontSize*1.2;
    line-height: $normaLineHeight;
    text-align: left;
    width: $gutterWidth*12;
    height: $gutterWidth*3;

    .footer-section-contact-heading {
      @include desktop-320-767 {
        width: 40%;
      }

      font-weight: bold;
      color: $primaryColor;
      margin: 0 0 $gutterWidth 0;
    }

    .footer-section-contact-subheading {
      @include desktop-320-767 {}

      font-weight: normal;
      color: $whiteText;

      .footer-section-contact-link {
        margin: 0 $gutterWidth*0.2;

        .footer-section-contact-logo {
          height: $gutterWidth*0.8;
        }

        &:first-child {
          margin-left: 0;
        }

      }
    }
  }
}

.section-image-heading {
  height: $gutterWidth;
  margin: $gutterWidth;
}

.section-paragraph {
  @include desktop-320-767 {
    padding: 0 $gutterWidth;

  }

  font-style: normal;
  font-weight: normal;
  font-size: $normalFontSize;
  line-height: $normaLineHeight;
  text-align: center;
  color: $whiteText;
  padding: 0 $gutterWidth*12;
}
